import React, { Component } from 'react'
import { 
  Row,
  Col,
  Card,
  CardBody,
  CardImg,
  Button,
  ListGroup,
  ListGroupItem,
  TabContent,
  TabPane
} from 'reactstrap'
import * as Feather from "react-feather";
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import {subRooter} from "../../../configs/vars";
// import Config from "../../../configs/config";
import EditCategoryPage from "./EditCategoryPage";

import { getParentData, deleteData } from "../../../redux/actions/category";
import { connect } from 'react-redux';


export class ListCategoriesPage extends Component {
  state = {
    active: '1',
    selectedObj: null
  }

  UNSAFE_componentWillMount() {
    this.props.getParentData();
  }

  handleDeleteCategory = (obj) => {
    this.props.deleteData(obj);
  }

  handleEditCategory = (obj, parentId) => {
    obj.parentId = parentId
    this.setState({
      selectedObj: obj
    }, ()=>this.toggle('2'))
  }

  toggle = (i) => {
    this.setState({
      active: i
    })
  }

  render() {
    const { selectedObj } = this.state;
    const { parentData } = this.props;
    return (
      <React.Fragment>
        <TabContent activeTab={this.state.active}>
          <TabPane  tabId="1">
            <Breadcrumbs
              breadCrumbTitle="Home"
              breadCrumbParent="Area"
              breadCrumbActive="Subjects"
            />
            <div className='mb-1'>
              <Button.Ripple color="primary"
                onClick={() => this.props.history.push(`${subRooter}/subject/create-new`)}
              >Create New Subject</Button.Ripple>
            </div>
            <Row>
              {
                parentData.map((category) => (
                  <Col  key={category._id} md='4' xs='12' sm='6'>
                    <Card>
                      <CardImg
                        className="img-fluid"
                        src={category.imgUrl}
                        alt="card image cap"
                      />
                      <CardBody>
                        <h3 className=''>{category.name}</h3>
                        <div className="mb-0">
                          {
                            category.children && category.children.length > 0 ?
                              <ListGroup>
                                {
                                  category.children.map((child) => (
                                    <ListGroupItem key={child._id}>
                                      <div className="justify-content-between w-100">
                                        <h5 className='float-left'>
                                          {child.name}
                                        </h5>
                                        <div className='float-right'>
                                          <Feather.Edit
                                            onClick={() => this.handleEditCategory(child, category._id)}
                                            size={20}
                                          />
                                          <Feather.Trash 
                                            onClick={() => this.handleDeleteCategory(child, category._id)}
                                            size={20}
                                          />
                                        </div>
                                      </div>
                                    </ListGroupItem>
                                  ))
                                }
                              </ListGroup>
                            : null
                          }
                        </div>
                        <div className="card-btns d-flex justify-content-between mt-2">
                          <Button.Ripple className="gradient-light-primary text-white" 
                            onClick={() => this.handleEditCategory(category)}
                            outline
                          >
                            Edit
                          </Button.Ripple>
                          <Button.Ripple color="danger"
                            onClick={() => this.handleDeleteCategory(category)}
                          >
                            Delete
                          </Button.Ripple>
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                ))
              }
            </Row>
          </TabPane>
          <TabPane  tabId="2">
              <EditCategoryPage selectedCategory={selectedObj} toggle={(i) => this.toggle(i)} />
          </TabPane>
        </TabContent>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return{
    parentData: state.category.allData
  }
}

const mapDispatchToProps = {
  getParentData,
  deleteData
}
export default connect(mapStateToProps, mapDispatchToProps)(ListCategoriesPage)