import React from 'react';
import { Button } from 'reactstrap';
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import EditForm from "./EditForm";

class EditCategoryPage extends React.Component {
  state = {
    category: {}
  }
  componentDidUpdate() {
    if(! Object.is(this.props.selectedCategory, this.state.category)) {
      this.setState({
        category: this.props.selectedCategory
      })
    }
  }

  render() {
    return (
      <React.Fragment>
        <Breadcrumbs
					breadCrumbTitle="Home"
					breadCrumbParent="Subject"
					breadCrumbActive="Edit Subject"
				/>
        <div className='mb-1'>
          <Button.Ripple color="primary"
            onClick={() => this.props.toggle('1')}
            outline
          >Back </Button.Ripple>
        </div> 
        <EditForm currectCategory={this.state.category} isEdit={true} {...this.props} />
      </React.Fragment>
    );
  }
}

export default EditCategoryPage;